<template>
    <div class="container1" id="container1"></div>
</template>

<script>
    import * as d3 from "d3";
    import {
      nodeList,
      linkLists,
      linksTextData,
      conceptHyperedge,
      conceptEntity
    } from "../../../assets/js/smallHyperedge";
    export default {
        name: "d3Force",
        props:{
            svgh:{
              type:Number,
              default:600
            },
          svgw:{
            type:Number,
            default:900
          }
         },
        data() {
            return {
                width: 1800,
                height: 1800,
                transformData:null,
                nodeClick: false,
              nodeList: nodeList,
              linkList: linkLists,
              linksTextData: linksTextData,
              conceptHyperedge: conceptHyperedge,
              conceptEntity: conceptEntity,
            }
        },
        mounted() {
          let strs = []
          this.nodeList.forEach(node => {
            if (node.level == 'reasonable' || node.level == 'concept') {
              strs.push({
                value: node.label,
                id: node.id
              })
            }
          })
          this.$emit("nodeSearch", strs)
            this.drawHyperedge(this.nodeList,this.linkList,this.linksTextData)
        },
        methods: {
          nodeInfoClick(item) {
            console.log(item)
            let e = document.createEvent("MouseEvents");
            e.initEvent("click", true, true);
            document.getElementById(item.id).dispatchEvent(e);
          },
          drawHyperedge(nodeList, linkList, linksTextData) {

            const _this = this
            // let svgw=_this.svgw
            // let svgh=_this.svgh
            // let widthScale = svgw / _this.width, heightScale = svgh / _this.height;
            const svg = d3.select(".container1").append("svg")
                .attr("viewBox", [0, 0, _this.width, _this.height])




            //三个矩形
            const back_act1=svg.append("path")
                .attr("stroke-width", 2)
                .attr("d", "M100,0L1800,0L1700,480L0,480L100,0")
                .attr("fill", "#0f2b6d")
                .attr("stroke","#0673ef")
            //.attr("opacity",0.1)
            const back_act2=svg.append("path")
                .attr("stroke-width", 2)
                .attr("d", "M100,500L1800,500L1700,1000L0,1000L100,500")
                .attr("fill", "#122761")
                .attr("stroke","#0673ef")
            const back_act3=svg.append("path")
                .attr("stroke-width", 2)
                .attr("d", "M100,1020L1800,1020L1700,1800L0,1800L100,1020")
                .attr("fill", "#122558")
                .attr("stroke","#0673ef")
            const act_title01 = svg.append("text")
                .attr("x",200)
                .attr("y",200)
                .attr("font-size", 50)
                .text("事理层")
                .style("fill","#fff")
            const act_title02 = svg.append("text")
                .attr("x",200)
                .attr("y",700)
                .attr("font-size", 50)
                .text("概念层")
                .style("fill","#fff")
            const act_title03 = svg.append("text")
                .attr("x",200)
                .attr("y",1500)
                .attr("font-size", 50)
                .text("实例层")
                .style("fill","#fff")

            const defs = svg.append("svg:defs");
            //箭头
            function marker(color){
              defs.append("marker")
                  .attr("id", color.replace("#", ""))
                  .attr("markerUnits","userSpaceOnUse")
                  .attr("viewBox", function () {
                    return "0 -2.5 5 5";
                  })//坐标系的区域
                  .attr("refX",6)//箭头坐标
                  .attr("refY", 0)
                  .attr("markerWidth", 16)//标识的大小
                  .attr("markerHeight", 16)
                  .attr("orient", "auto")//绘制方向，可设定为：auto（自动确认方向）和 角度值
                  .attr("stroke-width",8)//箭头宽度
                  .append("path")
                  .attr("d", function () {
                    return "M0,-1.25L5,0L0,1.25"
                  })//箭头的路径
                  .attr('fill',color);//箭头颜色
              return "url(" + color + ")";
            }

            const simulation = d3.forceSimulation(nodeList)
                .force("link", d3.forceLink(linkList).id(d => d.id))
                .force("charge", d3.forceManyBody().strength(-10))
                .force("center", d3.forceCenter(_this.width / 2, _this.height / 5))
                .force("y", d3.forceY())
                //.force("x",d3.forceX())
                .force("collision", d3.forceCollide([25]).strength([1]))

            let paths = function () {
              let nodeLists = []
              _this.conceptHyperedge.forEach(data => {
                let list = []
                let nodeList = []
                nodeList.push(data)
                data.entities.forEach(entity => {
                  list.push(entity)
                })
                _this.conceptEntity.forEach(conceptEntity => {
                  list.forEach(id => {
                    if (id == conceptEntity.id) {
                      nodeList.push(conceptEntity)
                    }
                  })
                })
                nodeLists.push(nodeList)
              })
              return nodeLists
            }
            let path = paths()

            const color1 = _this.color();
            const pa = svg.append("g")
                .selectAll("path")
                .data(path)
                .enter()
                .append("path")
                .attr("stroke-width", 2)
                .attr("fill", color1)
                .attr("opacity", 0.5)
                .on("mouseenter", function (d) {
                  let color = _this.color()
                  d3.select(this).attr("fill", color)
                  d3.selectAll("circle").transition().duration(500)
                      .attr("fill",function (o) {
                        let flag = 0
                        d.forEach(data => {
                          if (o.index == data.index) {
                            flag = 1
                            return
                          }
                        })
                        return flag == 1 ? "#00BFFF" : "#e68761"
                      })
                      .attr("stroke",function (o) {
                        let flag = 0
                        d.forEach(data => {
                          if (o.index == data.index) {
                            flag = 1
                            return
                          }
                        })
                        return flag == 1 ? "#00BFFF" : "#e68761"
                      })
                })
                .on("mouseleave", function () {
                  d3.select(this).attr("fill", _this.color())
                  d3.selectAll("circle").transition().duration(500)
                      .attr("fill", "#e68761").attr("stroke", "#e68761")
                })
            const link = svg.append("g")
                .attr("stroke", "#0673ef")
                .attr("stroke-opacity", 0.6)
                .selectAll("line")
                .data(linkList)
                .join("line")
                .attr("stroke-width", 2)
                .on("mouseenter", function (d) {
                  d3.select(this).attr("stroke", "#FFD700").attr("marker-end", marker("#FFD700"))
                  d3.selectAll(".linkText").transition().duration(500)
                      .attr("fill", function (o) {
                        if (d.index == o.index) {
                          return  "#FFD700"
                        }
                        return "#439282"
                      })
                })
                .on("mouseleave", function () {
                  d3.select(this).attr("stroke", "#0673ef").attr("marker-end", marker("#0673ef"))
                  d3.selectAll(".linkText").transition().duration(500).attr("fill", "#439282")
                })

            const linkText = svg.append("g")
                .selectAll("linkText")
                .data(linkList)
                .enter()
                .append("text")
                .text(function (d) {
                  let text = ""
                  linksTextData.forEach(data => {
                    if (data.id == d.relation) {
                      text = data.label
                    }
                  })
                  return text
                })
                .attr("class", "linkText")
                .attr("font-size", 8)
                .attr("fill", "#439282")
            const node = svg.append("g")
                .attr("stroke", "#e68761")
                .attr("stroke-width", 2)
                .selectAll("circle")
                .data(nodeList)
                .join("circle")
                .attr("r", 15)
                .attr("id",d => d.id)
                .attr("fill", "#d07b59")
                .on("click",function (d) {
                  let data = {}
                  for(let s in d){
                    if (!(s in ['x','y','vx','vy'])) {
                      data[s] = d[s]
                    }
                  }
                  _this.$emit("getNodeInfo", data)
                  if (d3.select(this).attr("r") == 15) {
                    d3.selectAll("circle").transition().duration(500)
                        .attr("r",function (o) {
                          return d.index == o.index ? 25 : 15
                        })
                        .attr("fill", function (o) {
                          return d.index == o.index ? "#FFD700" : "#d07b59"
                        })
                        .attr("stroke", function (o) {
                          return d.index == o.index ? "#FFD700" : "#d07b59"
                        });
                    d3.select(this).attr("r", 25).attr("fill", "#FFD700")
                    d3.selectAll("line").transition().duration(500)
                        .style("opacity", function (o) {
                          return o.source === d || o.target === d ? 1 : 0;
                        });
                  } else {
                    d3.select(this).attr("r", 15).attr("fill", "#d07b59").attr("stroke", "#d07b59")
                    d3.selectAll("line").transition().duration(500)
                        .style("opacity", 1);
                  }

                })
                .call(_this.drag(simulation))
            const text = svg.append("g")
                .selectAll("text")
                .data(nodeList)
                .enter()
                .append("text")
                .attr("text-anchor", "middle")
                .text(d => d.label)
                .attr("dx", -2)
                .attr("font-size", 12)
                .style("fill","#fff")

            simulation.on("tick", () => {

              pa     //画区域

                  .attr("d", function (data) {

                    let lsit = [];

                    data.forEach(function (d) {

                      if (d.level === "concept") {
                        lsit.push({
                          "x": Math.max(20 + 100, Math.min(d.x, _this.width - 20 - 100)),
                          "y": Math.min(Math.max(d.y + 400, 500 + 20),
                              1000 - 20)
                        })
                      }
                      if (d.level === "instance") {
                        lsit.push({
                          "x": Math.max(20 + 100, Math.min(d.x, _this.width - 20 - 100)),
                          "y": Math.min(Math.max(d.y + 900, 1020 + 20),
                              1800 - 20)
                        })
                      }

                    })

                    let lis = [];
                    lis = _this.convexHull(lsit);

                    let nodeList = [];

                    for (let j = 0; j < lis.length; j++) {
                      nodeList.push({
                        x: lis[j].x + 20,
                        y: lis[j].y
                      })
                      nodeList.push({
                        x: lis[j].x - 20,
                        y: lis[j].y
                      })
                      nodeList.push({
                        x: lis[j].x,
                        y: lis[j].y + 20
                      })
                      nodeList.push({
                        x: lis[j].x,
                        y: lis[j].y - 20
                      })
                    }
                    let nodeTest = _this.convexHull(nodeList);
                    let controlPointA = [];
                    let controlPointB = [];

                    for (let i = 0;i < nodeTest.length;i++){
                      let a = nodeTest[i-1];
                      let b = nodeTest[i+2];
                      let c = nodeTest[i+1];
                      if (i === 0){
                        a = nodeTest[nodeTest.length-1];
                        b = nodeTest[i+2];
                        c = nodeTest[i+1];
                      }else if(i === nodeTest.length-2){
                        a = nodeTest[i-1];
                        b = nodeTest[0];
                        c = nodeTest[i+1];
                      }else if (i === nodeTest.length-1){
                        a = nodeTest[i-1];
                        b = nodeTest[1];
                        c = nodeTest[0];
                      }else{
                        a = nodeTest[i-1];
                        b = nodeTest[i+2];
                        c = nodeTest[i+1];
                      }
                      // controlPointA.push({
                      //     x: nodeTest[i].x + 0.2 * (c.x-a.x),
                      //     y: nodeTest[i].y + 0.2 * (c.y-a.y)
                      // })
                      // controlPointB.push({
                      //     x: c.x - 0.2 * (b.x-nodeTest[i].x),
                      //     y: c.y - 0.2 * (b.y-nodeTest[i].y)
                      // })
                      controlPointA[i] = {
                        x: nodeTest[i].x + 0.15 * (c.x-a.x),
                        y: nodeTest[i].y + 0.15 * (c.y-a.y)
                      }
                      controlPointB[i] = {
                        x: c.x - 0.15 * (b.x-nodeTest[i].x),
                        y: c.y - 0.15 * (b.y-nodeTest[i].y)
                      }
                    }
                    let str = "M ";
                    for (let j = 0;j < nodeTest.length;j++){
                      str += nodeTest[j].x+","+nodeTest[j].y + " C" + controlPointA[j].x + "," +
                          controlPointA[j].y + " " + controlPointB[j].x + "," +
                          controlPointB[j].y + " ";

                    }
                    str += nodeTest[0].x + "," + nodeTest[0].y;
                    return str;
                  });
              link
                  .attr("marker-end", marker("#0673ef"))
                  .attr("x1", d => Math.max(20 + 100, Math.min(d.source.x, _this.width - 20 - 100)))
                  .attr("y1", function (d) {
                    if (d.source.level == "reasonable") {
                      const dx = Math.max(20, Math.min(d.source.y, 480 - 20));

                      return dx;

                    } else if (d.source.level == "concept") {
                      const dx = Math.min(Math.max(d.source.y + 400, 500 + 20),
                          1000 - 20);
                      //console.log(dx);
                      return dx;

                    } else if (d.source.level == "instance") {
                      const dx = Math.min(Math.max(d.source.y + 900, 1020 + 20),
                          1800 - 20);
                      //console.log(dx);
                      //const dx = _this.height*0.6 + 120 + (_this.height*0.4-160)*num;
                      return dx;

                    }
                  })
                  .attr("x2", d => Math.max(20 + 100, Math.min(d.target.x, _this.width - 20 - 100)))
                  .attr("y2", function (d) {
                    if (d.target.level == "reasonable") {
                      const dx = Math.max(20, Math.min(d.target.y, 480 - 20));
                      //console.log(dx);
                      return dx;

                    } else if (d.target.level == "concept") {
                      const dx = Math.min(Math.max(d.target.y + 400, 500 + 20),
                          1000 - 20);
                      //console.log(dx);
                      return dx;

                    } else if (d.target.level == "instance") {
                      const dx = Math.min(Math.max(d.target.y + 900, 1020 + 20),
                          1800 - 20);
                      //const dx = _this.height*0.6 + 120 + (_this.height*0.4-160)*num;
                      //console.log(dx);
                      return dx;

                    }
                  })
              // .attr("opacity", function (d) {
              //     if (nodesGroup2.indexOf(d.source)!==-1 && nodesGroup2.indexOf(d.target)!==-1){
              //         return 0;
              //     }else if (nodesGroup3.indexOf(d.source)!==-1 && nodesGroup3.indexOf(d.target)!==-1){
              //         return 0;
              //     }else {
              //         return 1;
              //     }
              // });

              linkText
                  .attr("x", function (d) {
                    let text = ""
                    linksTextData.forEach(data => {
                      if (data.id == d.relation) {
                        text = data.label
                      }
                    })
                    return (Math.max(20 + 100, Math.min(d.source.x, _this.width - 20 - 100))+Math.max(20 + 100, Math.min(d.target.x, _this.width - 20 - 100)))/2 - text.length / 2
                  })
                  .attr("y", function (d) {
                    let dsy = 0
                    let dty = 0
                    if (d.source.level == "reasonable") {
                      dsy = Math.max(20, Math.min(d.source.y, 480 - 20));
                    } else if (d.source.level == "concept") {
                      dsy = Math.min(Math.max(d.source.y + 400, 500 + 20),
                          1000 - 20);
                    } else if (d.source.level == "instance") {
                      dsy = Math.min(Math.max(d.source.y + 900, 1020 + 20),
                          1800 - 20);
                    }
                    if (d.target.level == "reasonable") {
                      dty = Math.max(20, Math.min(d.target.y, 480 - 20));
                    } else if (d.target.level == "concept") {
                      dty = Math.min(Math.max(d.target.y + 400, 500 + 20),
                          1000 - 20);
                    } else if (d.target.level == "instance") {
                      dty = Math.min(Math.max(d.target.y + 900, 1020 + 20),
                          1800 - 20);
                    }

                    return (dsy + dty) / 2
                  })

              node//点固定区域
                  .attr("cx", d => Math.max(20 + 100, Math.min(d.x, _this.width - 20 - 100)))
                  .attr("cy", function (d) {
                    if (d.level == "reasonable") {
                      const dx = Math.max(20, Math.min(d.y, 480 - 20));
                      //console.log(dx);
                      return dx;

                    } else if (d.level == "concept") {
                      //console.log(d.y);
                      const dx = Math.min(Math.max(d.y + 400, 500 + 20),
                          1000 - 20);
                      //console.log(dx);
                      return dx;

                    } else if (d.level == "instance") {

                      const dx = Math.min(Math.max(d.y + 900, 1020 + 20),
                          1800 - 20);
                      //const dx = _this.height*0.6 + 120 + (_this.height*0.4-160)*num;
                      //console.log(dx);
                      return dx;
                    }
                  }).attr("x", this.cx)
                  .attr("y", this.cy)
              // .attr("opacity", function (d) {
              //     if (d.uuid>=90000){
              //         return 0;
              //     }else if ('Eventid' in d){
              //         return 0;
              //     }else{
              //         return 1;
              //     }
              // })
              // .attr("stroke-width", function (d) {
              //     if (d.uuid>=90000){
              //         return 0;
              //     }else if ('Eventid' in d){
              //         return 0;
              //     }else{
              //         return 1;
              //     }
              // })

              text
                  .attr("x", d => Math.max(20 + 100, Math.min(d.x, _this.width - 20 - 100)))
                  .attr("y", function (d) {
                    if (d.level == "reasonable") {
                      const dx = Math.max(20, Math.min(d.y, 480 - 20));
                      //console.log(dx);
                      return dx;

                    } else if (d.level == "concept") {
                      const dx = Math.min(Math.max(d.y + 400, 500 + 20),
                          1000 - 20);
                      //console.log(dx);
                      return dx;

                    } else if (d.level == "instance") {
                      const dx = Math.min(Math.max(d.y + 900, 1020 + 20),
                          1800 - 20);
                      //const dx = _this.height*0.6 + 120 + (_this.height*0.4-160)*num;
                      //console.log(dx);
                      return dx;
                    }
                  })
              // .attr("opacity", function (d) {
              //     if (d.uuid>=90000){
              //         return 0;
              //     }else if ('Eventid' in d){
              //         return 0;
              //     }else{
              //         return 1;
              //     }
              // })
            })
            link.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            pa.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            text.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            node.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            linkText.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            back_act1.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            back_act2.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            back_act3.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            act_title01.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            act_title02.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            act_title03.attr("transform", "translate(433.6276121909756,57.60697440896706) scale(0.5)");
            svg.call(d3.zoom().on("zoom", function () {

              let transform = d3.event.transform;
              link.attr("transform", transform);
              pa.attr("transform", transform);
              text.attr("transform", transform);
              node.attr("transform", transform);
              linkText.attr("transform", transform);
              back_act1.attr("transform", transform);
              back_act2.attr("transform", transform);
              back_act3.attr("transform", transform);
              act_title01.attr("transform", transform);
              act_title02.attr("transform", transform);
              act_title03.attr("transform", transform);
              //d3.selectAll("path").attr("transform", transform);
            }))
          },
          drag(simulation) {
            function dragstarted(d) {
              if (!d3.event.active) simulation.alphaTarget(0.3).restart();
              d.fx = d.x;
              d.fy = d.y;
            }

            function dragged(d) {
              d.fx = d3.event.x;
              d.fy = d3.event.y;
            }

            function dragended(d) {
              if (!d3.event.active) simulation.alphaTarget(0);
              // d.fx = null;
              // d.fy = null;
              d.fx = d3.event.x;
              d.fy = d3.event.y;
            }

            return d3.drag()
                .on("start", dragstarted)
                .on("drag", dragged)
                .on("end", dragended);
          },
          color() {
            let rand=['#4A60D7'+
            '','#00782d','#226078','#238b49','#b53ad4']
            let n = Math.floor(Math.random() * rand.length + 1)-1;

            //let rand = '#' + ('00000' + (Math.random() * 0x1000000 << 0).toString(16)).slice(-6);
            return rand[n];
          },
          convexHull(arr) {

            const n = arr.length;

// There must be at least 3 points
            if (n < 3) {

              return arr;

            }

            const hull = [];

            let l = 0;

            for (let i = 0; i < n; i++) {

              if (arr[i].x < arr[l].x) {

                l = i;

              }

            }

            let p = l, q;

            do {

              hull.push(arr[p]);

              q = (p + 1) % n;

              for (let i = 0; i < n; i++) {

                if (this.orientation(arr[p], arr[i], arr[q]) === 2) {

                  q = i;

                }

              }

              p = q;

            } while (p !== l);

            return hull;

          },
          orientation(p, q, r) {

            const val = (q.y - p.y) * (r.x - q.x) - (q.x - p.x) * (r.y - q.y);

            if (val === 0) {

              return 0;

            }

            return (val > 0) ? 1 : 2;

          },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

    svg {
        background-color: aqua;
    }
    .changeRect01{
        transform: skew(-10deg);
    }
</style>
