<template>
  <div class="homepage">
<!--  left start-->
<div class="left">
  <div class="visual_left">
    <div class="visual_box" style="max-width: 90%">
      <div class="visual_title">
        <span>节点搜索</span>
        <img src="../../assets/images/ksh33.png">
      </div>
      <div class="">
        <el-autocomplete
            v-model="state"
            :fetch-suggestions="querySearchAsync"
            placeholder="请输入内容"
            @select="handleSelect"
        ></el-autocomplete>
        <el-button class="btnGroup" style="margin-left: 10px" @click="searchNode">搜索</el-button>
      </div>
    </div>



      <div class="visual_box" style="max-width: 90%">
        <div class="visual_title">
          <span>节点信息</span>
          <img src="../../assets/images/ksh33.png">
        </div>
        <div class="visual_conBot_left_bot">
          <ul style="margin-left: 20px">
            <li v-for="(param,key,index) in node" :key="index">
              {{key}} : {{param}}
            </li>
          </ul>

        </div>
      </div>

  </div>
</div>
<div class="center">
  <!--con start    -->
  <div class="visual_con">
    <div class="visual_conBot" id="visual_conBot">
      <img class="visual_conBot_l" src="../../assets/images/ksh42.png">
      <img class="visual_conBot_2" src="../../assets/images/ksh43.png">
      <img class="visual_conBot_3" src="../../assets/images/ksh44.png">
      <img class="visual_conBot_4" src="../../assets/images/ksh45.png">

      <div class="visual_chart">
        <d3-force ref="d3Force" @getNodeInfo="getNodeInfo" :svgh="height" :svgw="width" @nodeSearch="nodeSearch"></d3-force>
      </div>
    </div>

  </div>
  <!--con end    -->
</div>

  </div>
</template>

<script>

import D3Force from "@/components/index/indexComponents/d3Force(1)";

import {dataReset} from "@/assets/js/d3-force";
export default {
  name: "homePage",
  components: { D3Force},
  data() {
    return {
      restaurants: [],
      state: '',
      timeout:  null,
      nodeInfo: {},
      allNode: [],
      node: {},
      height:null,
      width:null
    }
  },
  created() {

  },
  mounted() {
    let o=document.getElementById('visual_conBot')
    this.height=o.offsetHeight
    this.width=o.offsetWidth
    console.log(this.height,this.width)

  },
  methods: {
    handleSelect(item) {
      this.node = item
      console.log(item);
    },
    querySearchAsync(queryString, cb) {
      let restaurants = this.allNode;
      let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 3000 * Math.random());
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },

    getNodeInfo(d) {

      let params = ['x', 'y', 'vx', 'vy', 'fx', 'fy']
      params.forEach(param => {
        delete d[param]
      })
      this.node = d
      console.log(d)
},

    nodeSearch(strs) {
      this.allNode = strs
    },
    searchNode() {
      if (this.state.length==0) {
        return
      }
      this.$refs.d3Force.nodeInfoClick(this.node)
    }

  }
}
</script>
