let reasonableEntity = require('../smallJson/reasonable-entity.json')
let conceptEdge = require('../smallJson/concept-edge.json')
let conceptRelation = require('../smallJson/concept-relationship.json')
export let conceptEntity = require('../smallJson/concept-entity.json')
export let conceptHyperedge = require('../smallJson/concept-hyperedge.json')
let instanceEdge = require('../smallJson/instance-edge.json')
let instanceEntity = require('../smallJson/instance-entity.json')
let instanceRelation = require('../smallJson/instance-relationship.json')
let reasonableEdge = require('../smallJson/reasonable-edge.json')
let reasonableRelationship = require('../smallJson/reasonable-relationship.json')
let resonable2concept = require('../smallJson/reasonable2concept.json')

let index = 0
let conceptHyperedgeRelationship = []
conceptHyperedge.forEach(relationship => {
    relationship.entities.forEach(entity => {
        conceptHyperedgeRelationship.push({
            id: "C2CLe" + index++,
            source: relationship.id,
            target: entity
        })
    })
})
let concept2instance = []
let j = 1
instanceEntity.forEach(node => {
    concept2instance.push({
        id: "C2I" + j++,
        target: node.id,
        source: node.concept_id,
        level: "concept2instance"
    })
})

export let nodeList = reasonableEntity.concat(conceptEntity,instanceEntity,conceptHyperedge)

export let linkLists = reasonableEdge.concat(conceptEdge,instanceEdge,conceptHyperedgeRelationship,resonable2concept,concept2instance)

export let linksTextData = []
linksTextData = conceptRelation.concat(reasonableRelationship, instanceRelation)
export default{
    nodeList,
    linkLists,
    linksTextData,
    conceptHyperedge,
    conceptEntity
}
